<template>
    <div class="header">
        <div class="left">
            <span class="iconfont">&#xe64e;</span>
        </div>
        <div class="search">
            <span class="iconfont">&#xe7c4;</span>            
            请输入城市/景点/游玩主题
        </div>
        <div class="right">
            <router-link to="/city">
                {{this.currentCity}}
                <span class="iconfont">&#xe6f0;</span>
            </router-link>
        </div>
    </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name:"HomeHeader",
    computed:{
        //将state中的city数据项映射到city计算属性中
        //...mapState(['city'])
        //如果下标不一致，可以使用{}
        ...mapState({currentCity:"city"})
    }
}
</script>

<style scoped>
    .header{
        display: flex;
        justify-content: space-between; /*两端对齐*/
        height: .8rem;
        background-color: #00bcd4;
        line-height: .8rem;
        color: #fff;
    }
    .search{
        flex: 1;                       /*占领剩余空间*/
        color: #ccc;
        background-color: #fff;
        border-radius: 5px;
        margin: .08rem;
        line-height: 32px;
        padding-left: .16rem;
    }
    .right a{
        color: #fff;
    }
</style>


