<template>
    <div class="icon-list">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item,index) in pages" :key="index">
                <div class="icon" v-for="(icon,i) in item" :key="i">
                    <div class="icon-img">
                        <img :src="icon.imgUrl" alt="">
                    </div>
                    <div class="icon-desc">
                        {{icon.desc}}
                    </div>
                </div>
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
export default {
    name:"HomeIconList",
    props:{
        list:Array  // 9个元素  [0 1 2 3 4 5 6 7 8]
    },
    computed:{
        //分页显示图标列表
        pages(){
            let pages = []; 
            //遍历list数组
            this.list.forEach((item,index) => {
                //将前8个元素放到上面的数组中
                // 0- 7  下标都是0
                // 8-15  下标都是1
                var page = Math.floor(index / 8);  
                if(!pages[page]){
                    pages[page] = [];
                }
                //如果该数组已经存在了
                pages[page].push(item);
            })
            return pages;
            /*
                pages = [
                    0:[0 1 2 3 4 5 6 7]
                    1:[8]
                ]
            */
        }
    }
}
</script>

<style scoped>
    .icon-list >>> .mint-swipe-indicator{
        opacity: 0.6;
    }
    .icon-list >>> .mint-swipe-indicator.is-active{
        background: #ccc;
    }
    .icon-list{
        height: 250px;        
    }
    .icon{
        width: 25%;
        float: left;
        box-sizing: border-box;
        text-align: center;
        padding: 10px;
    }
    .icon-img img{
        width: 100%;
    }
    .icon-desc{
        margin-top: 10px;
    }
</style>


