<template>
    <div class="city-list" ref="wrapper">
        <div>
            <!-- 当前城市 -->
            <div class="current">
                <h2 class="title">当前城市</h2>
                <div class="button-list">
                    <div class="button-wrapper">
                        <div class="button">{{this.city}}</div>
                    </div>
                    
                </div>
            </div>

            <!-- 热门城市 -->
            <div class="hot">
                <h2 class="title">热门城市</h2>
                <div class="button-list">
                    <div class="button-wrapper" v-for="(item,index) in hot" :key="index"
                        @click="handleCityClick(item.name)">
                        <div class="button">{{item.name}}</div>
                    </div>                
                </div>
            </div>

            <!-- 城市列表 -->
            <div class="list" v-for="(item,index) in cities" :key="index" :ref="index">
                <h2 class="title">{{index}}</h2>
                <ul>
                    <li class="item" 
                    v-for="(e,index) in item" 
                    :key="index"
                    @click="handleCityClick(e.name)"
                    >{{e.name}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
import {mapState,mapMutations} from 'vuex'
export default {
    name:"CityList",

    props:{
        cities:Object,
        hot:Array,
        letter:String
    },
    computed:{
        ...mapState(['city'])
    },
    mounted(){
        let wrapper = this.$refs.wrapper
        this.scroll = new BScroll(wrapper)
    },
    updated () {
        this.startY = this.$refs['A'][0].offsetTop
    },
    watch:{
        letter(){
            if(this.letter){
                const element = this.$refs[this.letter][0]
                console.log(this.$refs)
                this.scroll.scrollToElement(element)
            }
        }
    },
    methods:{
        handleCityClick(city){
            //this.$store.commit("changeCity",city)
            this.changeCity(city)
            this.$router.push("/")
        },
        ...mapMutations(['changeCity'])
    }
}
</script>

<style scoped>
    .title{
        line-height: 30px;
        background-color: #eee;
    }
    .button-list{
        display: flex;  /*默认都显示在一行*/
        flex-wrap: wrap; /*换行*/        
    }
    .button-wrapper{
        width: 33.33%;       
    }
    .button{
        border: 1px solid #ccc;
        border-radius: 5px;
        box-sizing: border-box;
        text-align: center;
        padding: 5px 0;
        margin: 5px 15px;
    }
    .item{
        line-height: 35px;
        padding-left:10px
    }
    .city-list{
        position: absolute;
        left: 0;
        top: 80px;
        bottom: 0;
        right: 0;
        overflow: hidden;
        z-index: -999;
    }
</style>


