<template>
    <div class="home">
        <!-- 头部组件 -->
        <home-header></home-header>

        <!-- 轮播图组件 -->
        <home-swipper :list="swipeList"></home-swipper>

        <!-- 图标列表组件 -->
        <home-icon-list :list="iconList"></home-icon-list>

        <!-- 热销推荐组件 -->
        <home-recommend :list="rexiaoList"></home-recommend>

        <!-- 周末去哪儿 -->
        <home-weekend :list="weekList"></home-weekend>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import axios from 'axios'
import HomeHeader from './components/Header'
import HomeSwipper from './components/Swipper'
import HomeIconList from './components/IconList'
import HomeRecommend from './components/Recommend'
import HomeWeekend from './components/Weekend'

export default {
    name:"Home",
    components:{
        HomeHeader,
        HomeSwipper,
        HomeIconList,
        HomeRecommend,
        HomeWeekend
    },
    computed:{
        ...mapState(['city'])
    },
    data(){
        return {
            swipeList:[],
            //图标列表
            iconList:[],
            // 热销推荐列表
            rexiaoList:[],
            //周末去哪
            weekList:[]
        }
    },

    methods:{
        //获取首页数据
        getHomeData(){
            axios.get("/tour/static/api/index.json")
            .then((res) => {
                console.log(res.data.data)
                var data = res.data.data;
                this.swipeList = data.swiperList;
                this.iconList = data.iconList;
                this.rexiaoList = data.recommendList;
                this.weekList = data.weekendList;
            })
        }
    },
    mounted(){
        this.lastCity = this.city
        this.getHomeData();
    },
    activated(){        
        if(this.lastCity !== this.city){
            this.lastCity = this.city
            this.getHomeData()
        }
    }
}
</script>

<style scoped>

</style>


