<template>
    <div class="alphabet">
        <ul class="list">
            <li class="item" 
                v-for="(item,key) of letters" 
                :key="key"
                :ref="item"
                @click="handleLetter"
                @touchstart.prevent="handleTouchStart"
                @touchmove="handleTouchMove"
                @touchend="handleTouchEnd"
                >
                {{item}}
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name:"CityAlphabet",
    props:{
        cities:Object
    },
    data(){
        return {
            touchStatus: false,
            startY:0
        }
    },
    computed: {
        letters () {
        const letters = []
        for (let i in this.cities) {
            letters.push(i)
        }
        return letters
        }
    },
    //数据更新并DOM修改之后
    updated () {
        this.startY = this.$refs['A'][0].offsetTop
    },
    methods:{
        handleLetter(e){
            //向外触发事件,并传递数据，让City.vue监听该事件
            this.$emit('change',e.target.innerText)
        },
        handleTouchStart () {
            this.touchStatus = true
        },
        handleTouchMove (e) {
            if(this.touchStatus){
                //先计算A字母距离父元素顶部的高度
                //const startY = this.$refs['A'][0].offsetTop;

                //console.log(startY)
                //获取手指的位置, 需要减去头部的高度
                const touchY = e.touches[0].clientY - 100;

                //每个字母高20px
                const index = Math.floor((touchY - this.startY) / 20)

                if(index >= 0 && index < this.letters.length){
                    this.$emit("change",this.letters[index])
                }          
            }
        },
        handleTouchEnd(){
            this.touchStatus = false
        }
    }
}
</script>

<style scoped>
    .alphabet{
        display: flex;
        flex-direction: column;
        justify-content:center;
        position: absolute;
        right: 0;
        bottom: 0;
        top:100px;      
    }
    .list .item{
        line-height: 20px;
        width: 30px;
        text-align: center;
        color: #00bcd4;
    }
</style>


